<template>
  <div class="outBox">

          <van-nav-bar title="商品详情" left-text="返回" left-arrow @click-left="onClickLeft" fixed />
    
    <div class="tab_img">
      <img :src="imgUrl" />
    </div>
    <!-- 商品名称 -->
    <div class="text_box">
      <div class="commodityName">{{productData.name}}</div>
    <!-- 单价 -->
      <div class="priceBox clearfix">
        <div class="price fl">
          ￥
          <span>{{productData.price}}</span>
        </div>
        <div class="stepperBox fr">
          <van-stepper v-model="value" theme="round" button-size="22" disable-input />
        </div>
      </div>
    <!-- 商品规格 -->
      <div class="typeSelect">
        <div class="type clearfix" v-for="(item,index) in lists" :key="index">
          <div class="title fl">{{item.typeName}}</div>
          <div class="selectBox fl clearfix" v-for="(itm,ind) in item.typeSelect" :key="ind+'11'">
            <div
              class="fl"
              @click="item.num = ind"
              :class="{detail_demo:item.num==ind}"
            >{{itm.select}}</div>
          </div>
        </div>

      </div>
      <!-- 商品描述 -->
      <div class="product_description">
        <div class="title">商品描述</div>
        <div class="description_text">
          <p v-for="(item,index) in productDetail" 
          :key="index">{{item}}</p>
        </div>
      </div>
    </div>

    <!-- 加入购物车拦 -->
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" color="#07c160" />
      <van-goods-action-icon icon="cart-o" 
      text="购物车" 
      :badge="shopcartRows == 0 ? '' : shopcartRows" 
      @click="tagShopcart"
      />
      <van-goods-action-icon icon="star" text="已收藏" :color="ishas?'#f80':'#ccc'" @click="collectBtn" />
      <van-goods-action-button type="warning" text="加入购物车" @click="addProduct(!isbuy)"/>
      <van-goods-action-button type="danger" text="立即购买" @click="nowShopping(isbuy)" />
    </van-goods-action>
  </div>
</template>

<script>
import "../assets/less/detail.less";
export default {
  data() {
    return {
      value: 1, //步进器
      pid:null,
      lists: [],
      productData: {}, //商品详情数据
      productDetail:[],//商品描述
      imgUrl:null,//商品大图
      ishas:false,//是否收藏
     
      shopcartRows: 0,//购物车条数，并非每个商品数量
      isbuy:true,//立即购买标识
    };
  },
  created() {
    let pid = this.$route.query.pid;
    //获取商品详情数据
    this.getProductsDetail(pid);
    //查询已收藏的商品
    this.find_collectAfter(this.pid)
    //查询商品条数
    this.find_productStrip()
  },
  methods: {
    //返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },

    //获取商品详情
    getProductsDetail(pid) {
        this.pid = pid
        
        
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method:'GET',
        url:'/productDetail',
        params:{
          appkey:this.appkey,
          pid:pid
        }
      }).then(result => {
        
        if(result.data.code == 600){

            let productDatas = result.data.result[0]
            productDatas.desc = productDatas.desc.split('\n')
            this.productDetail = productDatas.desc//商品描述
            this.productData = productDatas//商品数据
            this.imgUrl = productDatas.large_img
            

            //商品规格
          let productRule = ['tem', 'sugar', 'milk', 'cream'];
            productRule.forEach(e => {
                if(productDatas[e]!=''){
                    let obj = {
                        typeName:productDatas[e+'_desc'],
                        typeSelect:[],
                        num : 0
                    }
                    let arr = productDatas[e] = productDatas[e].split('/')
                    arr.forEach(v => {
                        let objs = {}
                        objs.select = v
                        obj.typeSelect.push(objs)
                    })
                    this.lists.push(obj)
                }
            })

            
            



        }
        this.$toast.clear();
      }).catch(() => {
        
        this.$toast.clear();
      })
    },

    //点击收藏/取消收藏
    collectBtn(){
        
        //获取token
      let tokenString = localStorage.getItem('__Tk');

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

        let url = this.ishas?'/notlike':'/like'
        this.axios({
            method:'POST',
            url,
            data:{
                appkey:this.appkey,
                pid:this.pid,
                tokenString
            }
        }).then(result => {
            
            this.$toast.clear()
            if(result.data.code==800||result.data.code==900){
                this.ishas = result.data.code==800
                this.$toast.success(result.data.msg)
            }else {
                //都没有的话跳转到登录页面
                this.$router.push({name:'login'})
            }
            
        }).catch(() => {
            
            this.$toast.clear()
        })

    },

    //查询当前pid商品是否被收藏
    find_collectAfter(pid){
        //获取token
      let tokenString = localStorage.getItem('__Tk');

        //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
            method:'GET',
            url:'/findlike',
            params:{
                appkey:this.appkey,
                pid,
                tokenString
            }
        }).then(result => {
            
            this.$toast.clear()
            if(result.data.code == 1000){
                //如果result.data.result数组有长度 说明当前商品已被收藏
                this.ishas = result.data.result.length>0
            }else {
                //如果token无效 则跳转到登录页面
                this.$router.push({name:'login'})
            }
            
        }).catch(() => {
            
            this.$toast.clear()
        })
    },

    //查询购物车商品条数
    find_productStrip(){

        //获取token
      let tokenString = localStorage.getItem('__Tk');

        //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
          method:'GET',
          url:'/findAllShopcart',
          params:{
            appkey:this.appkey,
            tokenString
          }
      }).then(result => {
            
            this.$toast.clear()
            if(result.data.code == 5000){
                this.shopcartRows = result.data.result.length
            }
            
        }).catch(() => {
            
            this.$toast.clear()
        })
    },

    //添加购物车商品
    addProduct(isbuy){
        
        //点击之前最好判断一下用户是否登录，否则跳转到登录页面

        //获取token
      let tokenString = localStorage.getItem('__Tk');

        //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

        //找出你选中的规格 把它拼接成 热/半糖 格式
        
        let rule = []
        this.lists.forEach(e => {
            let current = e.num
           rule.push(e.typeSelect[current].select)
        })        
        // 
        
      this.axios({
          method:'POST',
          url:'/addShopcart',
          data:{
            pid:this.pid,
            tokenString,
            appkey:this.appkey,
            count:this.value,//表示单种商品的个数
            rule:rule.join('/'),
          }
      }).then(result => {
            
            this.$toast.clear()
            
            
        if (result.data.code == 3000) {

          if (result.data.status == 1) {
            this.shopcartRows++;
          }
          
          this.$toast(result.data.msg);

          //
          if(isbuy){
            setTimeout(() => {
              this.$router.push({name:'clearing',query:{sids:result.data.sid}})
            },300)
          }
        }
            
        }).catch(() => {
            
            this.$toast.clear()
        })
    },

    //立即购买  先加入购物车 再跳转结算页面
    nowShopping(isbuy){
      this.addProduct(isbuy)
    },

    //跳转到购物车
    tagShopcart(){
        this.$router.push({name:'shopcart'})
    }
  }
};
</script>

<style lang="less" scoped>
</style>